import axios from 'axios';
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import * as Yup from 'yup';
export default function Register() {
  let navigate = useNavigate()
  const [loading,setLoading] = useState(false) 
  const [message,setmessage] = useState('') 
 
  async function submitForm(values) {
    
   setLoading(true) 
  try {
    const {data} = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`,values)
    if(data.message=='success')
   { 
    setLoading(false)
    navigate('/login')
  }
  } catch (error) {
    setmessage(error.response.data.message)
    setLoading(false)
  }
  }

  let validateScheme = Yup.object({
    name: Yup.string().min(3, 'too chart').max(5, 'too max').required('this field is required'),
    email: Yup.string().email('Invalid email').required('this field is required'),
    password: Yup.string().required('this field is required').matches(/^[A-Z][0-9]{6}$/, 'not match'),
    rePassword: Yup.string().oneOf([Yup.ref('password')], 'not match').required('this field is required'),
    phone: Yup.string().required('this field is required').matches(/^01[0125][0-9]{8}$/, 'not match')
  })

  const formik = useFormik({
    initialValues: { name: '', email: '', password: '', rePassword: '', phone: '' },
    validationSchema: validateScheme,
    onSubmit: submitForm
  })



  return (
    <div className='w-75 m-auto p-3'>
      <h4 className='my-2'>Register Now:</h4>
      {message?<h6 className='alert alert-danger'>{message}</h6>:''}
      <form className='my-4' onSubmit={formik.handleSubmit}>
        <label htmlFor="">name:</label>
        <input type="text" className='form-control mb-1' name='name' onChange={formik.handleChange} value={formik.values.name} onBlur={formik.handleBlur} />

        {formik.errors.name && formik.touched.name  ? <p className='alert alert-danger'>{formik.errors.name}</p> : ''}
        <label htmlFor="">email:</label>
        <input type="text" className='form-control mb-1' name='email' onChange={formik.handleChange} value={formik.values.email} onBlur={formik.handleBlur} />

        {formik.errors.email  && formik.touched.email ? <p className='alert alert-danger'>{formik.errors.email}</p> : ''}
        <label htmlFor="">password:</label>
        <input type="text" className='form-control mb-1' name='password' onChange={formik.handleChange} value={formik.values.password} onBlur={formik.handleBlur} />


        {formik.errors.password && formik.touched.password  ? <p className='alert alert-danger'>{formik.errors.password}</p> : ''}
        <label htmlFor="">rePassword:</label>
        <input type="text" className='form-control mb-1' name='rePassword' onChange={formik.handleChange} value={formik.values.rePassword} onBlur={formik.handleBlur} />
        {formik.errors.rePassword && formik.touched.rePassword  ? <p className='alert alert-danger'>{formik.errors.rePassword}</p> : ''}
        <label htmlFor="">phone:</label>
        <input type="text" className='form-control mb-1' name='phone' onChange={formik.handleChange} value={formik.values.phone} onBlur={formik.handleBlur} />

        {formik.errors.phone && formik.touched.phone  ? <p className='alert alert-danger'>{formik.errors.phone}</p> : ''}
        <button disabled={!(formik.isValid && formik.dirty)} className='my-2 btn btn-success float-end' type='submit'>{loading?<span>Loading <i class="fa-solid fa-spinner fa-spin text-white"></i></span>:'Register'}</button>
      </form>
    </div>
  )
}
