import React, { useContext, useEffect, useState } from 'react'
import { Link, NavLink, useNavigate } from 'react-router-dom'
import logo from '../../images/freshcart-logo.svg'
import { CartContext } from '../../Context/CartContext'
export default function Navbar() {



  let { numOfCartItems, setnumOfCartItems, isLogin, stayLogin } = useContext(CartContext)
  let navigate = useNavigate()
  useEffect(() => {
    stayLogin()
  }, [isLogin])
  function logout() {
    localStorage.removeItem('token')
    setnumOfCartItems(0)
    navigate('/login')
  }

  return (
    <nav className="navbar navbar-expand-lg bg-body-tertiary">
      <div className="container">
        <Link className="navbar-brand">
          <img src={logo} alt="" />
        </Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <NavLink to='' className="nav-link">Home</NavLink>
            </li>
            <li className="nav-item">
              <NavLink to='products' className="nav-link">Products</NavLink>
            </li>
            <li className="nav-item">
              <NavLink to='allorders' className="nav-link">Orders</NavLink>
            </li>
            <li className="nav-item">
              <NavLink to='cart' className="nav-link">Cart
                <i className='fa-solid fa-cart-shopping'></i>
                <span className='d-inline-block text-center  rounded-circle' style={{
                  width: '20px', height: '20px', backgroundColor: '#45C046', lineHeight: '20px',
                  position: 'relative', bottom: '10px'
                }}>{numOfCartItems}</span>
              </NavLink>
            </li>


          </ul>
          <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
            {isLogin ?

              <li className="nav-item">
                <a className="nav-link" onClick={logout} style={{ cursor: 'pointer' }}>Logout</a>
              </li> :
              <>
                <li className="nav-item">
                  <NavLink to='register' className="nav-link">Register</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink to='login' className="nav-link">Login</NavLink>
                </li></>

            }

          </ul>

        </div>
      </div>
    </nav>
  )
}
