import { useFormik } from 'formik'
import React, { useContext, useEffect } from 'react'
import { CartContext } from '../Context/CartContext'
import axios from 'axios'

export default function Checkout() {

    async function CheckOut(cartId,values)
    {
       let {data} = await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}?url=http://localhost:3000`,
       {
        shippingAddress:values
       },{
        headers:{token:localStorage.getItem('token')}
       }) 
       return data
    }
    let {cartId} = useContext(CartContext)
   
    async function handleForm(values) {
    let data= await CheckOut(cartId,values)
    if(data.status=='success')
    window.location.href=data.session.url
    }
    let formik = useFormik({
        initialValues: {
            details: '',
            city: '',
            phone: ''
        },
        onSubmit: handleForm
    })

    useEffect(()=>{
      
    },[])

    return (

        <>
            <form action="" className='my-5 w-75 mx-auto' onSubmit={formik.handleSubmit}>
                <input type="text" onChange={formik.handleChange} value={formik.values.details} className='form-control my-2' placeholder='details' id="details" />
                <input type="text" onChange={formik.handleChange} value={formik.values.city} className='form-control my-2' placeholder='city' id='city' />
                <input type="tel" onChange={formik.handleChange} value={formik.values.phone} className='form-control my-2' placeholder='phone' id="phone" />
                <button className='btn btn-success' type='submit'>Pay</button>
            </form>
        </>
    )
}
