import React, { Suspense, lazy } from 'react'
import { createBrowserRouter, RouterProvider } from 'react-router-dom'
import Layout from '../Layout/Layout'
import Login from '../Login/Login'
import Register from '../Register/Register'
import { CartContextProvider } from '../../Context/CartContext'
import { Toaster } from 'react-hot-toast'
import Checkout from '../../Checkout/Checkout'
import { ProtectedRoute } from '../../ProtectedRoute'
import Orders from '../Orders/Orders'


export default function App() {

  

  const Home = lazy(() => import('../Home/Home'))
  const Cart = lazy(() => import('../../Cart/Cart'))
  const Products = lazy(() => import('../Products/Products'))
  const ProductsDetails = lazy(() => import('../ProductDetails/ProductsDetails'))
  const routes = createBrowserRouter(
    [

      {
        path: '', element: <Layout></Layout>, children: [
          { index: 'true', element: <Suspense fallback={<>loading..</>}><Home></Home></Suspense> },
          { path: 'login', element: <Login></Login> },
          { path: 'register', element: <Register></Register> },
          {
            path: 'products', element: <Suspense fallback={<>loading..</>}><Products></Products></Suspense>
          },
          {
            path: 'cart', element: <Suspense fallback={<>loading..</>}><Cart></Cart></Suspense>
          },
          { path: 'checkout', element:<ProtectedRoute><Checkout></Checkout></ProtectedRoute>  },
          { path: 'allorders', element:<Orders></Orders>  },
          {
            path: 'products', children: [{
              path: ':id', element: <Suspense fallback={<>loading..</>}><ProductsDetails></ProductsDetails></Suspense>
            }]
          }
        ]
      }
    ])

  return (
    <CartContextProvider>
      <RouterProvider router={routes}></RouterProvider>
      <Toaster />
    </CartContextProvider>
  )
}
