import axios from "axios";
import { createContext, useEffect, useState } from "react";


export const CartContext = createContext(null)

export function CartContextProvider({ children }) {



let [isLogin, setLogin] = useState(false)
 function stayLogin()
 {
   if(localStorage.getItem('token'))
   setLogin(true)
   else
   setLogin(false)
 }

 useEffect(()=>{stayLogin()},[isLogin])
    let BaseURL = 'https://ecommerce.routemisr.com/api/v1'

    const [numOfCartItems, setnumOfCartItems] = useState(0)
    const [cartId, setcartId] = useState(null)
    async function addToCart(productId) {
       
        try {
            const { data } = await axios.post(`${BaseURL}/cart`, {
                productId
            }, {
                headers: {
                    token: localStorage.getItem('token')
                }
            })
            if (data.status == 'success') {
                setnumOfCartItems(data.numOfCartItems)
                return data
            }
        } catch (error) {
            console.log(error);
        }
      
    }
    async function getFromCart() {
        try {
            const { data } = await axios.get(`${BaseURL}/cart`, {
                headers: {
                    token: localStorage.getItem('token')
                }
            })
            if (data.status == 'success') {
                setnumOfCartItems(data.numOfCartItems)
                setcartId(data.data._id)
                return data
            }
        } catch (error) {
            return error
        }
    }

    useEffect(()=>{
        getFromCart()
    },[])

    async function UpdateCart(productId,count) {
        try {
            const { data } = await axios.put(`${BaseURL}/cart/${productId}`, {

                count
            }, {
                headers: {
                    token: localStorage.getItem('token')
                }
            })
            if (data.status == 'success') {
                setnumOfCartItems(data.numOfCartItems)
                return data
            }
        } catch (error) {
            return error
        }
    }
    async function DeleteItem(productId) {
        try {
            const { data } = await axios.delete(`${BaseURL}/cart/${productId}`, {
                headers: {
                    token: localStorage.getItem('token')
                }
            })
            if (data.status == 'success') {
                setnumOfCartItems(data.numOfCartItems)
                return data
            }
        } catch (error) {
            return error
        }
    }



    return <CartContext.Provider value={{stayLogin,isLogin,setnumOfCartItems,cartId,DeleteItem, addToCart, getFromCart, numOfCartItems, UpdateCart }}>
        {children}
    </CartContext.Provider>
}
